import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})
export class SharedService {

  emailKey = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
  phoneNumberKey = /^\(\d{3}\) \d{3}-\d{4}$/;
  isUserLoggedIn: boolean = false;
  userEmail: string;
  isMenuOpen: boolean = false;
  isPageMenuOpen: boolean = false;

  getEncryptedValue(value: string) {
    const key = CryptoJS.enc.Base64.parse(environment.cryptojs_key);
    const iv = CryptoJS.enc.Base64.parse(environment.cryptojs_iv);

    const encrypted = CryptoJS.AES.encrypt(value, key, {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    });

    const encryptedString = encrypted.toString();
    return encryptedString;
  }

  toggleMenu() {
    this.isMenuOpen = !this.isMenuOpen;
  }

  togglePageMenu() {
    this.isPageMenuOpen = !this.isPageMenuOpen;
  }

  public selectedAddress = new BehaviorSubject<any>({});
  selectedAddress$: Observable<any> = this.selectedAddress.asObservable();
}
