import { Component, OnDestroy, OnInit } from '@angular/core';
import { FooterComponent } from '../shared/components/footer/footer.component';
import { NavigationStart, Router, RouterOutlet } from '@angular/router';
import { SharedService } from '../shared/services/shared.service';
import { URLS } from '../shared/utils/urls';
import { Subscription } from 'rxjs';
import { AppService, AuthService, Cookie, HeatfleetHeaderService, LoginInfo } from '@monorepo/utils';
import { HfToastService } from '@monorepo/ui';
import { CookieHelper } from '../shared/services/cookie.helper';
import { environment } from '../../environments/environment';
import { HeatfleetHeaderComponent } from '@monorepo/common-component';
import { isCompanyApp } from '../../../src/app/shared/utils/app.static'
@Component({
  selector: 'app-master',
  standalone: true,
  templateUrl: './master.component.html',
  styleUrl: './master.component.scss',
  imports: [RouterOutlet, FooterComponent, HeatfleetHeaderComponent]
})
export class MasterComponent implements OnInit, OnDestroy {

  loginInfo: Subscription;
  isCompanyApp: boolean = isCompanyApp;

  constructor(
    public sharedService: SharedService,
    private authService: AuthService,
    private toastService: HfToastService,
    private router: Router,
    public cookieHelper: CookieHelper,
    public heatfleetHeaderService: HeatfleetHeaderService,
    public appService: AppService,
  ) { }

  ngOnInit() {
    this.heatfleetHeaderService.initHeatFleetHeader('secure', URLS, environment.client_url, '/assets/icons/logo.svg', '/assets/icons/company-logo.svg', '/assets/icons/company-name-logo.svg');
    if (this.router.url === `/${URLS.LOGIN}` || this.router.url === URLS.LOGIN) {
      this.loginProcess();
    }
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
        window.scrollTo(0, 0);
        if (this.loginInfo) {
          this.loginInfo.unsubscribe();
        }
        if (event.url === `/${URLS.LOGIN}` || event.url === URLS.LOGIN) {
          this.loginProcess();
        }
        if (this.heatfleetHeaderService.isMenuOpen) {
          this.heatfleetHeaderService.toggleMenu();
        }
        if (this.heatfleetHeaderService.isPageMenuOpen) {
          this.heatfleetHeaderService.togglePageMenu();
        }
      }
    });
  }

  loginProcess() {
    this.loginInfo = this.authService.isLoginDone$.subscribe((res: LoginInfo) => {
      if (res.isLoginDone) {
        if (!this.appService.isApp) {
          if (!environment.is_local) {
            this.authService.processLoginResponse(res.loginResponse, '.heatfleet.com');
          } else {
            this.authService.processLoginResponse(res.loginResponse);
          }
          const zipCode = this.cookieHelper.getCookie(Cookie.ZIP_CODE);
          let gallons = this.cookieHelper.getCookie(Cookie.GALLONS);
          if (gallons == '') {
            gallons = '100';
          }
          if (zipCode !== '') {
            this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/${zipCode}/0/0/4/${gallons}`]);
          } else {
            if (!environment.is_local) {
              window.location.href = environment.client_url;
            } else {
              this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/13107/0/0/4/100`]);
            }
          }
        } else {
          const expirationDate = new Date(Date.now() + 7 * 24 * 60 * 60 * 1000); // Expires in 7 days
          const expires = expirationDate.toUTCString();
          this.authService.processLoginResponse(res.loginResponse, 'localhost', '/', expires);
          const zipCode = this.cookieHelper.getCookie(Cookie.ZIP_CODE);
          let gallons = this.cookieHelper.getCookie(Cookie.GALLONS);
          if (gallons == '') {
            gallons = '100';
          }
          if (zipCode !== '') {
            this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/${zipCode}/0/0/4/${gallons}`]);
          } else {
            this.router.navigate(['/']);
          }
        }
        this.toastService.toastMessage.next({ title: 'Success', msg: 'Login successful', type: 'success' });
        this.authService.isLoginDone.next({ isLoginDone: false, loginResponse: null });
      }
    });
  }

  closeMenu() {
    if (this.heatfleetHeaderService.isMenuOpen) {
      this.heatfleetHeaderService.toggleMenu();
    }
    if (this.heatfleetHeaderService.isPageMenuOpen) {
      this.heatfleetHeaderService.togglePageMenu();
    }
  }

  ngOnDestroy() {
    if (this.loginInfo) {
      this.loginInfo.unsubscribe();
    }
  }
}
