import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CookieHelper } from '../services/cookie.helper';
import { AppService, Cookie, StoreService } from '@monorepo/utils';
import { URLS } from '../utils/urls';
import { environment } from '../../../environments/environment';

@Injectable({
    providedIn: 'root'
})

export class SecureGuard {

    constructor(
        private router: Router,
        public cookieHelper: CookieHelper,
        public appService: AppService,
        private storeService: StoreService,
    ) { }

    canActivate(): boolean {
        this.storeService.checkKeyInStore('orderParams', (orderParams) => {
            if (!orderParams) {
                const zipCode = this.cookieHelper.getCookie(Cookie.ZIP_CODE);
                let gallons = this.cookieHelper.getCookie(Cookie.GALLONS);
                if (gallons == '') {
                    gallons = '100';
                }
                if (!this.appService.isApp) {
                    if (zipCode !== '') {
                        this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/${zipCode}/0/0/4/${gallons}`]);
                    } else {
                        if (!environment.is_local) {
                            window.location.href = environment.client_url;
                        } else {
                            this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/13107/0/0/4/100`]);
                        }
                    }
                } else {
                    if (zipCode !== '') {
                        this.router.navigate([`${URLS.OIL_PROVIDER_SELECTOR}/${zipCode}/0/0/4/${gallons}`]);
                    } else {
                        this.router.navigate(['/']);
                    }
                }
                return false;
            } else {
                return true;
            }
        });
        return true;
    }
}