import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CookieHelper } from '../services/cookie.helper';
import { URLS } from '../utils/urls';
import { AuthService } from '@monorepo/utils';

@Injectable({
    providedIn: 'root'
})

export class AuthGuard {

    constructor(
        private router: Router,
        public cookieHelper: CookieHelper,
        public authService: AuthService
    ) { }

    canActivate(): boolean {
        const isLoggin = this.checkIfUserIsLoggedIn();
        if (isLoggin) {
            this.authService.makeUserCache();
        } else {
            this.authService.clearUserCache();
        }
        return isLoggin;
    }

    private checkIfUserIsLoggedIn(): boolean {
        try {
            const authToken = this.cookieHelper.getAuthToken();
            const verifyAuthToken = this.cookieHelper.verifyAuthToken(authToken);
            if (verifyAuthToken) {
                return true;
            }
            this.router.navigate([URLS.LOGIN]);
            return false;
        } catch (error) {
            this.router.navigate([URLS.LOGIN]);
            return false
        }
    }

}