import { Routes, mapToCanActivate } from '@angular/router';
import { MasterComponent } from './master.component';
import { URLS } from '../shared/utils/urls';
import { LoginGuard } from '../shared/guard/login.guard';
import { AuthGuard } from '../shared/guard/auth.guard';
import { UserGuard } from '../shared/guard/user.guard';
import { HomeGuard } from '../shared/guard/home.guard';
import { SecureGuard } from '../shared/guard/secure.guard';

export const routes: Routes = [
  {
    path: '',
    component: MasterComponent,
    children: [
      {
        path: URLS.ROOT,
        // canActivate: mapToCanActivate([HomeGuard]),
        loadComponent: () => import('./home/home.component').then(c => c.HomePageComponent)
      },
      {
        path: URLS.LOGIN,
        canActivate: mapToCanActivate([LoginGuard]),
        loadComponent: () => import('@monorepo/auth').then(c => c.AuthComponent)
      },
      {
        path: `${URLS.OIL_PROVIDER_SELECTOR}/:zipCode/:tankSize/:customerType/:orderType/:quantity`,
        canActivate: mapToCanActivate([UserGuard]),
        loadComponent: () => import('./oil-provider-selector/oil-provider-selector.component').then(c => c.OilProviderSelectorComponent)
      },
      {
        path: URLS.ORDER_LOGIN,
        canActivate: mapToCanActivate([UserGuard, SecureGuard]),
        loadComponent: () => import('./order-login/order-login.component').then(c => c.OrderLoginComponent)
      },
      {
        path: URLS.CONFIRM_ADDRESS,
        canActivate: mapToCanActivate([AuthGuard, SecureGuard]),
        loadComponent: () => import('./confirm-address/confirm-address.component').then(c => c.ConfirmAddressComponent)
      },
      {
        path: URLS.USER_ACCOUNT,
        canActivate: mapToCanActivate([AuthGuard]),
        loadComponent: () => import('./user-account/user-account.component').then(c => c.UserAccountComponent)
      },
      {
        path: URLS.ORDERS,
        canActivate: mapToCanActivate([AuthGuard]),
        loadComponent: () => import('./orders/orders.component').then(c => c.OrdersComponent)
      },
      {
        path: `${URLS.ORDER_DETAILS}/:orderId`,
        canActivate: mapToCanActivate([AuthGuard]),
        loadComponent: () => import('./order-details/order-details.component').then(c => c.OrderDetailsComponent)
      },
      {
        path: URLS.FUEL_ADDITIVE,
        canActivate: mapToCanActivate([UserGuard, SecureGuard]),
        loadComponent: () => import('./fuel-additive/fuel-additive.component').then(c => c.FuelAdditiveComponent)
      },
      {
        path: URLS.PAYMENT,
        canActivate: mapToCanActivate([UserGuard, SecureGuard]),
        loadComponent: () => import('./payment/payment.component').then(c => c.PaymentComponent)
      },
      {
        path: URLS.DELIVERY_DETAILS,
        canActivate: mapToCanActivate([UserGuard, SecureGuard]),
        loadComponent: () => import('./delivery-details/delivery-details.component').then(c => c.DeliveryDetailsComponent)
      }
    ]
  },
];
